.cart-icon {
  width: 45px;
  height: 45px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;

  .shopping-icon {
    width: 24px;
    height: 24px;
}
   
  .item-count {
    //absolute allows us to put the number 
    //right on the shopping icon 

    position: absolute; 
    font-size: 10px;
    font-weight: bold;
    bottom: 12px;
  }
  
   
}
