.checkout-item {
  width: 100%;
  display: flex;
  min-height: 100px;
  border-bottom: 1px solid darkgrey;
  padding: 15px 0;
  font-size: 20px;
  align-items: center;

  .image-container {
    width: 23%;
    height : 100%;
    padding-right: 15px;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .name,
  .quantity,
  .price {
    width: 23%;
  }

  .quantity {

    display: flex;    
    flex-direction: row;
     
    .arrow {
      cursor: pointer;
    }

    .value {
      margin : 0 10px; //left and right 
    }

  }

  .remove-button {
    padding-left: 12px;
    cursor: pointer;
  }
}
